.button {
  text-align: center;
  padding: 15px 0;
  height: 50px;
  border-radius: 32px;
  cursor: pointer;

  &.green {
    color: white;
    background-color: $color-green;

    &.shadow {
      // box-shadow: 7px 7px 30px 0 rgba(97, 214, 196, 0.7);
   
    }

    &:hover {
      background-color: lighten($color-green, 5%);
    }
  }

  &.white {
    width: 226px;
    color: $color-green;
    background-color: white;
    box-shadow: 7px 7px 30px 0 rgba(200, 200, 200, 0.7);

    &:hover {
      color: white;
      background-color: $color-green;
    }
  }
}